.nav {
  position: absolute;
  top:8%;
  left: 75%;
  font-family: 'Bree Serif';
  
}

.nav a {
  text-decoration: none;
  padding-right: 40%;
  color: blueviolet;
}
.nav a:hover {
  color: white;
}

.nav a {
  
  
  text-decoration: none;
  
  
  display: inline-block;
  padding: 15px 20px;
  position: relative;
}
.nav a:after {    
  background: none repeat scroll 0 0 transparent;
  bottom: 0;
  content: "";
  display: block;
  height: 2px;
  left: 50%;
  position: absolute;
  background: #fff;
  transition: width 0.3s ease 0s, left 0.3s ease 0s;
  width: 0;
}
.nav a:hover:after { 
  width: 100%; 
  left: 0; 
}
